import {SceneObjectBehavior} from './scene-object-behavior';
import {ISceneViewRenderer, ISceneView} from './scene.model';
/**
 * A component that can be added to a [[SceneView]] to add additional
 * rendering to it.
 */
export class SceneViewComponent extends SceneObjectBehavior implements ISceneViewRenderer {
  beforeFrame(view: ISceneView, elapsed: number) {
    // nothing
  }

  renderFrame(view: ISceneView, elapsed: number) {
    // nothing
  }

  afterFrame(view: ISceneView, elapsed: number) {
    // nothing
  }

}



